<template>
    <div id="header-container">
        <div id='tempheader'>
            <!-- <img :src="require('../assets/logo.png')" alt='Logo' width=209 height=125/> -->
            <img :src="require('../assets/logo2.png')" alt='Logo' width=240 height=106/>
            <div id="descriptor">
                <h1>ДОСТАВКА ПИЦЦЫ. МОСКВА</h1>
            </div>
            <div id="phone">
                <h2>+7 (495) 792-17-00</h2>
            </div>            
        </div>
        
        <div id="header">
            <div id="header-menu">
                <app-header-link 
                    url="/main" 
                    text="МЕНЮ"                     
                    v-scroll-to='{el: "#categ-common-pizza", offset: longOffset}'
                />                
                <app-header-link 
                    v-for='categ in getCategories' 
                    :key='categ.id' 
                    :url='"/catalog" + categ.url' 
                    :text="categ.text | uppercase" 
                    :id="categ.id"
                    v-scroll-to='{el: "#categ-"+categ.id, offset: shortOffset}'
                />                  
            </div>            
            <div id="cart-link" v-if='this.$store.state.ordered.length > 0'>                
                <app-header-link url="/order" :text="`В корзине ${getOrderItemsCount} шт. на ${getOrderSum} ₽`" />                
            </div>
        </div>
    </div>
</template>

<script>
    import HeaderLink from "./HeaderLink";
    import { mapGetters, mapMutations } from 'vuex';
    export default {

        data() {
            return {
                shortOffset: -50,
                longOffset: -200,                
            }
        },

        components: {
            'app-header-link': HeaderLink,
        },

        created() {            
            window.addEventListener('scroll', this.handleScroll);
        },

        beforeDestroy() {
            window.removeEventListener('scroll', this.handleScroll);
        },

        methods: {
            ...mapMutations([
                'filterByCategory',
            ]),
            handleScroll(event) {                        
                const scrolled = window.pageYOffset 
                    || document.documentElement.scrollTop;
                const tempheader = document.getElementById('tempheader');                
                if (scrolled > (3*tempheader.offsetHeight)) return;                
                const menu = document.getElementById('header');                
                const catalog = document.getElementById('body-container');   
                catalog.style.marginTop = ( tempheader.offsetHeight + menu.offsetHeight ) + 'px';
                this.shortOffset = 0 - menu.offsetHeight;
                this.longOffset = 0 - menu.offsetHeight - tempheader.offsetHeight;
                
                let header = document.getElementById('header-container');                
                if (scrolled > tempheader.offsetHeight) {
                    header.style.top = (0-tempheader.offsetHeight) + 'px';
                } else {
                    header.style.top = (0-scrolled) + 'px';
                }
            }
        },

        computed: {
            ...mapGetters([
                'getOrderItemsCount',
                'getOrderSum',
                'getCategories',
                'isMobile',
            ]),
        }
    }
</script>

<style scoped>

    h1 {
        font-size: 1.5em;
    }

    h2 {
        font-size: 1.7em;
        font-weight: bold;

    }

    h3 {
        font-size: 1.7em;        
    }
    
    #descriptor {
        min-width: 250px;
    }

    #phone {
        text-align: right;
        flex: 1;
    }

    #header-container {        
        position: fixed;
        top: 0;        
        width: 90vw;        
        max-width: 1060px;
    }

    #tempheader {        
        height: 150px;        
        display: flex;
        flex-flow: row wrap;            
        width: 100%;
        align-items: center;      
        background-color: white;            
    }

    #tempheader div {
        margin-left: 50px;        
    }

    #header {        
        display: flex;        
        width: 100%;                               
        background-color: white;
        flex-flow: row wrap;
        justify-content: space-between;
        align-items: center;
        box-shadow: 0px 5px 5px -4px #777;
        padding-bottom: 10px;
        padding-top: 10px;
    }

    #header-menu {        
        text-align: left;
        display: flex;
        flex-flow: row wrap;
    }

    #cart-link {        
        text-align: right;        
    }

    #cart-link a {
        border: none;
        padding: 7px;
        border-radius: 10px;        
        background-color: #f0712c;
        color: white;
        width: 250px;       
        transition: all 0.1s ease; 
    }

    #cart-link a:hover {
        background-color: #de601b;
    }



</style>






