<template>
  <router-link :class="linkClass" :to="url" >{{text}}</router-link>
</template>
<script>
  export default {    
    props: ['url', 'text', 'id'],

    created() {            
      window.addEventListener('scroll', this.isInViewport);
    },

    beforeDestroy() {
      window.removeEventListener('scroll', this.isInViewport);
    },

    data() {
      return {
        linkClass: 'link',
      }
    },

    methods: {
      isInViewport() {
        if (!this.id) {
          this.linkClass = 'link'
          return;
        }        
        const element = document.getElementById('categ-'+this.id)        
        const rect = element.getBoundingClientRect();        
        const html = document.documentElement;        
        console.log(this.id + ": " + rect.top);
        if (
          rect.top <= (window.innerHeight || html.clientHeight) &&
          rect.bottom >= 150
        ) {
          console.log('enter');
          this.linkClass = 'link-active'
        } else {
          console.log('free');
          this.linkClass = 'link'
        }
      }
    }
  }
</script>
<style scoped>
  .link {
    text-align: center;
    display: inline-block;
    padding: 10px;    
    color: #777;
  }
  .link-active {
    text-align: center;
    display: inline-block;
    padding: 10px;    
    color: #f0712c;   
  }
  .link:hover {
    color: black;   
  }
  
</style>




