<template>    
    <div id="catalog">
        <div :id='"categ-"+categ.id' v-for='categ in getItems' :key='categ.id'>
            <h1>{{categ.text | uppercase}}</h1>
            <div class="product-container">
                <app-product :key="item.id" v-for="item in categ.items" :item='item' />                                        
            </div>
        </div>        
        <transition name='delivery'>
            <div id="delivery-form-back" v-show="showDelivery" @click='hideDeliveryEvent'>
                <div id="delivery-form"> 
                    <app-delivery-form />
                </div>
            </div>
        </transition>
    </div>       
</template>
<script>    
    import Product from './Product';
    import DeliveryForm from './DeliveryForm';
    import { mapGetters, mapMutations } from 'vuex';
    export default {
        components: {
            'app-product': Product,
            'app-delivery-form': DeliveryForm,
        },
        methods: {
            ...mapMutations([
                'hideDelivery'
            ]),
            hideDeliveryEvent(event) {
                if (event.target.id == "delivery-form-back" ) this.hideDelivery();
            }
        },
        computed: {
            ...mapGetters([
                'getItems',
                'showDelivery'
            ]),
        }        
    }
</script>
<style scoped>
    #catalog {
        display: flex;
        flex-flow: column;
        align-items: center;
        justify-content: space-around;
        
    }
    h1 {
        padding: 20px;
        padding-top: 40px;          
        text-align: left;
        font-size: 1.5em;
    }
    .product-container {
        display: flex;
        flex-flow: row wrap;
        justify-content: left;     
              
    }
    #delivery-form-back {
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        background-color: #00000055;        
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 10;
    }
    #delivery-form {        
        display: block;
        flex: 1;
        max-width: 800px;
        padding: 20px;        
        background-color: white;
        border: 1px solid #DDD;         
    }
    .delivery-enter-active, .delivery-leave-active {
        transition: opacity 0.1s;
    }
    .delivery-enter, .delivery-leave-to {
        opacity: 0;
    }

</style>


