import itemsJson from '../assets/items.json'
import categoriesJson from '../assets/categories.json'
export default {
    isMobile: /Android|webOS|iPhone|iPod|Opera Mini/i.test(navigator.userAgent),        
    showDelivery: false,
    validToOrder: true,
    fio: '',
    validFio: true,
    comment: '',
    phone: '',    
    validPhone: true,
    street: '', 
    validStreet: true,    
    building: '',
    validBuilding: true,
    latitude: 0.0,
    longitude: 0.0,
    polygons: [
        {
            id: "1",
            path: [
                { lat: 55.645826, lng: 37.5972999 },
                { lat: 55.696826, lng: 37.5972999 },
                { lat: 55.696826, lng: 37.6782999 },
                { lat: 55.645826, lng: 37.6782999 },
            ]
        },
    ],
    inPolygons: 0, //0 - не задан, 1 - входит в зоу, 2 не входит в зону
    map: {},
    addrSearch: '',    
    items: itemsJson.items,        
    filtered: itemsJson.items,
    categories: categoriesJson.categories,
    ordered: [],    
    getOrderKey(item) {
        const id = item.id;
        const sizeId = item.sizes.find(it => it.selected == true).id;        
        const thicknessId = item.thickness.find(it => it.selected == true).id;        
        return `${id}:${sizeId}:${thicknessId}`;
    },    
    getItem(id) {
        return this.items.find(it => it.id === id)
    },
    getFilteredItem(id) {
        return this.filtered.find(it => it.id === id)
    },
}


