<template>
    <div id="order" v-show='getOrderItems.length > 0'>
        <!-- <h1>Ваш заказ</h1> -->
        <table id="order-items">            
            <app-order-item :key="item.orderKey" v-for="item in getOrderItems" :item='item' />                
        </table>
        <app-contact-form id="order-form"/>
        <app-delivery-form />        
        <button id="send-order" @click='formValidation'>ЗАКАЗАТЬ ПИЦЦУ</button>
    </div>    
</template>
<script>    
    import OrderItem from './OrderItem';
    import ContactForm from './ContactForm';
    import { mapGetters, mapMutations } from 'vuex';
    import DeliveryForm from './DeliveryForm.vue';
    export default {
        components: {
            'app-order-item': OrderItem,
            'app-contact-form': ContactForm,
            'app-delivery-form': DeliveryForm,
        },
        methods: {
            ...mapMutations([
                'formValidation',
            ]),
        },
        computed: {
            ...mapGetters([
                'getOrderItems',
            ]),
        }         
    }
</script>
<style scoped>
    h1 {
        text-align: left;
        font-size: 2em;
        padding: 10px;
    }
    #order {
        display: flex;      
        flex-flow: column nowrap;
        max-width: 800px;        
    }        
    #order-items {
        margin-top: 20px;
        margin-bottom: 40px;        
    }
    #send-order {
        border: none;
        display: block;
        border-radius: 10px;
        background-color: #f0712c;
        color: white;
        width: 100%;        
        padding: 20px;
        margin: 10px;
        margin-top: 20px;
        transition: all 0.1s ease;
    }
    #send-order:hover {
        background-color: #de601b;
    }
</style>


