<template>
    <div class="cta">
        <div class="price">{{item.sizes.find(it => it.selected == true ).price}} ₽</div>        
        <button v-if="!(item.quantity > 0)" class="button" @click="addToOrder(item)">В корзину</button>
        <div class="qauntity-cont" v-else>
            <button class="small-button" @click="delFromOrderByProduct(item.id)">&#8722;</button>
            <span class="quantity">{{item.quantity}}</span>
            <button class="small-button" @click="addToOrder(item)">&#65291;</button>
        </div>
    </div>

</template>
<script>    
    import { mapMutations } from 'vuex';
    export default {
        props: ['item'],
        methods: {
            ...mapMutations([
                'addToOrder',
                'delFromOrderByProduct'
            ])
        }
    }
</script>
<style scoped>
    .cta {
        display: flex;
        flex-flow: row nowrap;
        justify-content: space-between;
        /* margin: 20px 0 20px 0; */
        width: 300px;
        
    }

    .price {                
        font-size: 1.5em;  
        flex: 5;
        text-align: left;
        margin: 5px;
    }

    .button {            
        border: none;
        border-radius: 10px;
        background-color: #f0712c;
        color: white;
        flex: 8;        
        margin: 5px;
        transition: all 0.1s ease;
    }
    .button:hover {
        background-color: #de601b;
    }

    .qauntity-cont {
        flex: 8;
        display: flex;
        flex-flow: row nowrap;
        justify-content: space-between;
        width: 100px;        
        margin: 5px;
    }

    .small-button {            
        border: none;
        border-radius: 10px;
        background-color: #f0712c;
        color: white;
        min-width: 30px;                
        flex: 1;
        transition: all 0.1s ease;
    }

    .small-button:hover {
        background-color: #de601b;
    }

    .quantity {     
        border: none;           
        /* padding: 5px;    */
        font-size: 1.5em;    
        flex: 1;   
    }

    

</style>





