<template>
    <div class="container">
        <form action="SEND_ORDER">
            <h2>КОНТАКТ</h2>       
            <br>    
            <input :class='this.$store.state.validFio ? "" : "invalid"' type="text" placeholder="Имя" v-model='fio' autocomplete="off" />
            <br>    
            <input :class='this.$store.state.validPhone ? "" : "invalid"' type="text" placeholder="Телефон" v-model='phone' autocomplete="off" />                        
            <br>    
            <!-- <p v-if='!this.$store.state.validFio || !this.$store.state.validPhone' class='validation-text'>Нам важно знать, как с Вами связаться</p>             -->
            <textarea v-model='comment' placeholder="Комментарий"></textarea>
        </form>
    </div>
</template>
<script>
export default {
    computed: {
        comment: {
            get() {
                return this.$store.state.comment;
            },
            set(value) {
                this.$store.commit('updateComment', value);
            }
        },
        fio: {
            get() {
                return this.$store.state.fio;
            },
            set(value) {
                this.$store.commit('updateFio', value);
            }
        },
        phone: {
            get() {
                return this.$store.state.phone;
            },
            set(value) {
                this.$store.commit('updatePhone', value);
            }
        }
    }
}
</script>
<style scoped>    
    .validation-text {
        color: red;        
    }
    .container {
        text-align: left;        
        padding: 10px;        
    }
    input[type='text'], textarea {        
        outline: none;
        border-radius: 10px;
        padding: 10px;
        width: 61%;
        border: 1px solid #ccc;
        margin-bottom: 15px;        
    }
    input[type='text'].invalid {
        border: 1px solid red;
    }

    input[type='text']:focus, textarea:focus{
        outline: none;
        border: 1px solid #f0712c;        
    }
    textarea {
        display: block;
        width: 100%;        
        height: 50px;  
        margin-bottom: 20px;      
        margin-right: 10px;        
    }
    

</style>




