export default {
    getItems(state) {    
        let arr = [];            
        state.categories.forEach(categ => {
            categ.items = state.filtered.filter(it => it.category == categ.id );
            arr.push(categ);
        });
        return arr;
    },
    getCategories(state) {
        return state.categories;
    },
    getOrderItems(state) {
        return state.ordered;
    },
    getOrderItemsCount(state) {
        let sum = 0;
        state.ordered.forEach(item => {
            sum += item.quantity;
        });
        return sum;
    },
    getOrderSum(state) {
        let sum = 0;
        state.ordered.forEach(item => {
            sum += item.sizes.find(it => it.selected == true ).price * item.quantity;
        });
        return sum;
    },
    getStreet(state) {
        return state.street;
    },
    getBuilding(state) {
        return state.building;
    },    
    getLatLng(state) {
        return {lat: state.latitude, lng: state.longitude};
    },    
    showDelivery(state) {
        return state.showDelivery;
    },
    getPolygons(state) {
        return state.polygons;
    },
    getInPolygonsStatus(state) {        
        return state.inPolygons;
    },
    isMobile(state) {
        return state.isMobile;
    }
}


