<template>
    <tr class="order-item">
        <!-- <td><img class="image" :src="images('./' + item.image + '100.png')" /></td> -->
        <td class="text-container">
            <div class="name">{{item.name | uppercase}}</div>            
            <div class="description">
                <span v-show='size.selected' :key='size.id' v-for='size in item.sizes'>{{size.text | lowercase}}</span>,
                <span v-show='th.selected' :key='th.id' v-for='th in item.thickness'>{{th.text | lowercase}} тесто</span>
            </div>
            <div class="description">{{item.description | lowercase}}</div>                             
        </td>
        <td class="qauntity-cont">
            <button class="small-button" @click="delFromOrderByKey(item.orderKey)">&#8722;</button>
            <span class="quantity">{{item.quantity}}</span>
            <button class="small-button" @click="addToOrder(item)">&#65291;</button>
        </td>
        <td class="price">{{item.sizes.find(it => it.selected == true ).price * item.quantity}} ₽</td>
        <td>
            <button class="small-button" @click="delAllFromOrderByKey(item.orderKey)">&#10005;</button>
        </td>
    </tr>    
</template>

<script>  
    import ProductParam from './ProductParam';  
    import { mapMutations } from 'vuex';
    const imagesContext = require.context('../assets', true) 
    export default {    
        components: {
            'app-product-param': ProductParam,
        }, 
        props: ['item'],
        methods: {
            ...mapMutations([
                'addToOrder',
                'delFromOrderByKey',
                'delAllFromOrderByKey',
            ]),
        },
        data() {
            return {
                images: imagesContext, 
            }
        }
    }
</script>

<style scoped>

    td {
        padding: 10px;                
        text-align: left;
        vertical-align: middle;        
        border-bottom: 1px solid #DDD;
    }

    .order-item {    
        max-width: 600px;                        
    }

    .image {        
        width: 100px;
        height: 100px;        
        background-image: require('../assets/noimage.svg');
    }

    .name {        
        font-size: 1.2em;      
        margin-bottom: 10px;          
    }

    .description {        
        color: #777;        
    }

    .price {
        font-size: 1.5em;
        padding: 10px;
        min-width: 120px;
        text-align: right;
    }

    .qauntity-cont {                
        width: 120px;        
    }
    /* #f0712c; */
    .small-button {            
        border: none;
        border-radius: 10px; 
        background-color: white;
        color: #f0712c;
        padding: 7px 0px 7px 0px;
        width: 30px;       
        font-weight: bold;
        font-size: 1.2em; 
        transition: all 0.1s ease;
    }

    .small-button:hover {                    
        border-radius: 10px;         
        color: black;
    }

    .quantity {     
        border: none;   
        padding: 7px 5px 7px 5px;                        
        font-size: 1.5em;  
    }

</style>




