<template>
    <div class="delivery-container">
        
            <h2>АДРЕС</h2>      
            <br>                          
            <input 
                :class='this.$store.state.validStreet ? "" : "invalid"' id="input-street" 
                type="text" 
                placeholder="Улица"                 
                autocomplete="off" 
                :value="getStreet"
                @input="inputStreet"
                @keyup.enter="fetchLatLng"
            />
            <br>    
            <input 
                :class='this.$store.state.validBuilding ? "" : "invalid"' 
                type="text" 
                placeholder="Номер дома"                 
                autocomplete="off" 
                :value='getBuilding' 
                @input="inputBuilding"
                @keyup.enter="fetchLatLng"
            /> 
            <br>    
            <button class="button" @click='fetchLatLng'>Узнать возможность</button>            
            <br>    
            <button v-if='getInPolygonsStatus === 1 && showDelivery == true' class="button" @click='hideDelivery'>Закрыть</button>            
            <h3 v-if='getInPolygonsStatus === 2' class='validation-text'>Адрес доставки не входит в зону доставки</h3>                              
            <h3 v-if='getInPolygonsStatus === 1' class='success-text'>По этому адресу мы сможем доставить</h3>            
            <p v-if='!this.$store.state.validStreet || !this.$store.state.validBuilding' class='validation-text'>Нам важно знать, куда доставить пиццу</p>
            <br>                              
            <gmap-map 
                ref="map"
                class='map' 
                :center='{ lat: 55.655826, lng: 37.6172999 }' 
                :zoom='10' 
                :options='{gestureHandling: "none"}'
            > 
                <gmap-marker :position="getLatLng" />
                <gmap-polygon 
                    :key="polygon.id"
                    v-for="polygon in getPolygons"
                    :path="polygon.path"
                    :options="{
                        strokeWeight: 1,
                        strokeColor: '#f0712c',
                        fillColor: '#f0712c',
                    }" 
                />
            </gmap-map>
        
    </div>
</template>
<script>
import { mapGetters, mapMutations, mapActions } from 'vuex';
export default {

    methods: {        
        ...mapMutations([
            'updateStreet',
            'updateBuilding',
            'hideDelivery',
        ]),                
        ...mapActions([
            'fetchLatLng',
        ]),
        inputStreet(event) {
            this.$store.commit('updateStreet', { value: event.target.value, map: this.$refs.map.$mapObject });            
        },
        inputBuilding(event) {
            this.$store.commit('updateBuilding', { value: event.target.value, map: this.$refs.map.$mapObject });
        },
    },
    computed: {
        ...mapGetters([
            'getStreet',
            'getBuilding',
            'getLatLng',
            'getPolygons',
            'getInPolygonsStatus',
            'showDelivery',
        ]),
        kladrRequest() {
             return `http://kladr-api.ru/api.php?contentType=street&cityId=7700000100000&limit=30&query=`;
        },                
    }
}
</script>
<style scoped>   
    .validation-text {
        color: red;        
    } 
    .success-text {
        color: green;        
    } 
    .delivery-container {
        text-align: left;        
        padding: 10px;
    }    
    .map {
        width: 100%;        
        height: 300px;
    }
    input[type='text'] {        
        outline: none;
        border-radius: 10px;
        padding: 10px;
        border: 1px solid #ccc;
        margin-bottom: 20px;
        width: 61%;
    }
    input[type='text']:focus {
        outline: none;
        border: 1px solid #f0712c;        
    }
    input[type='text'].invalid {
        border: 1px solid red;
    }
    .button {
        border: none;
        border-radius: 10px;
        background-color: #f0712c;
        color: white;                
        padding: 10px;
        margin-bottom: 20px;
        width: 39%;
        min-width: 150px;
        transition: all 0.1s ease;
    }
    .button:hover {
        background-color: #de601b;
    }

</style>




