<template>
    <div class="product">        
        <img class="image" :src="images('./' + item.image + '300.png')" />
        <h3 class="name">{{item.name | uppercase }}</h3>
        <div class="description">{{item.description | lowercase}}</div> 
        <app-product-param :values='item.sizes' @change='changeSizes({id: item.id, values: arguments[0]})' />
        <app-product-param :values='item.thickness' @change='changeThickness({id: item.id, values: arguments[0]})' />
        <app-product-cta :item="item" />
    </div>
</template>

<script>
    import ProductCTA from './ProductCTA';
    import ProductParam from './ProductParam';
    import { mapMutations } from 'vuex';
    const imagesContext = require.context('../assets', true) 
    export default {
        components: {
            'app-product-cta': ProductCTA,
            'app-product-param': ProductParam,
        },
        props: ['item'],
        data() {
            return {
                images: imagesContext, 
            }
        },
        methods: {
            ...mapMutations([
                'changeSizes',
                'changeThickness',
            ])
        }
    }
</script>

<style scoped>

    .product {            
        display: flex;    
        flex-flow: column nowrap;        
        align-items: center;    
        margin: 50px 20px 50px 20px;        
        width: 300px;
    }

    .image {        
        width: 300px;
        height: 300px;        
        padding: 5px;                
        background-image: require('../assets/noimage.svg');
    }

    .name {
        flex: 1;
        font-size: 1.3em;
        padding-top: 10px;
        padding-bottom: 10px;        
        font-weight: normal;
    }

    .description {
        flex: 1;
        color: #777;        
        margin-bottom: 20px;        
    }

</style>




