<template>
    <div class='product-param'>        
        <button 
        v-for='value in values' 
        :key='value.id'         
        :class='value.selected ? "product-value-selected" : "product-value"' 
        @click='selectValue(value.id)'
        >
            {{value.text | lowercase }}
        </button>
    </div>
</template>
<script>
import _ from 'lodash';
export default {
    props: [
        'multi',
        'values'
    ],   
    methods: {
        selectValue(id) {                                              
            let newValues = JSON.parse(JSON.stringify(this.values));
            if (!this.multi) {                
                newValues = newValues.map(it => {
                    it.selected = false;   
                    return it;                 
                });
            }           
            const value = newValues.find(it => it.id === id);            
            value.selected = !value.selected;                  
            this.$emit('change', newValues);
        }
    }
}
</script>
<style scoped>
    .product-param {
        display: flex;     
        justify-content: center;
        align-items: center;    
        width: 300px;              
    }
    .product-value {
        flex: 1;
        outline: none;
        border: none;
        border: 1px solid #DDDDDD;
        border-radius: 10px;
        background-color: white;                
        padding: 5px 0px 5px 0px;
        margin: 5px;
        width: 80px;
        transition: all 0.1s ease;
    }
    .product-value-selected {
        flex: 1;
        outline: none;
        border: 1px solid #f0712c;
        border-radius: 10px;        
        background-color: white;
        color: #f0712c;
        padding: 5px 0px 5px 0px;
        width: 80px;
        margin: 5px;
    }
    .product-value:hover {
        border: 1px solid #777;
        color: #777
    }
</style>






